.terminal {
  background-color: #455059;
  min-height: 100vh;
  overflow: hidden;
}
.terminal__wrap {
  display: flex;
  flex-wrap: wrap;
  position: relative;
}
.terminal .sidebar {
  width: 300px;
  padding-right: 20px;
  padding-bottom: 85px;
  background-color: #455059;
}
.terminal .sidebar__item {
  margin-top: 42px;
}
.terminal .sidebar__item-name {
  color: #fff;
  font-size: 20px;
  font-weight: 500;
  margin: 0 0 19px;
  position: relative;
}
.terminal .sidebar__item-toggle {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  margin: auto;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: 0.3s;
  transform: rotate(180deg);
}
.terminal .sidebar__item-toggle .icon {
  width: 10px;
  height: 10px;
}
.terminal .sidebar__item-toggle.active {
  transform: rotate(0);
}
.terminal .sidebar__drop {
  padding: 0;
  margin: 0;
  list-style: none;
  display: none;
}
.terminal .sidebar__drop-item {
  border-top: 1px solid #4c5761;
  padding: 15px 0 16px 42px;
}
.terminal .sidebar__drop-item:last-of-type {
  border-bottom: 1px solid #4c5761;
}
.terminal .sidebar__drop a {
  font-weight: 500;
  font-size: 15px;
  color: #fff;
  position: relative;
  line-height: 1;
  transition: 0.3s;
}
.terminal .sidebar__drop a:hover {
  color: #14bed3;
}
.terminal .sidebar__drop a:hover svg {
  fill: #14bed3;
}
.terminal .sidebar__drop .icon {
  position: absolute;
  left: -38px;
  top: -2px;
  width: 24px;
  text-align: center;
}
.terminal .sidebar__drop .icon svg {
  transition: 0.3s;
  fill: #fff;
}
.terminal .content {
  background-color: #fff;
  width: calc(100% - 300px);
  position: relative;
  min-height: 100vh;
  color: #3b444c;
}
.terminal .content:before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 10000px;
  background-color: #fff;
  z-index: 0;
  pointer-events: none;
}
.terminal .content__info {
  position: relative;
  z-index: 2;
  font-size: 11px;
  line-height: 1.2;
  font-weight: 300;
  padding: 42px 30px 30px;
}
.terminal .content__info:after {
  position: absolute;
  content: '';
  left: 0;
  bottom: 0;
  width: 10000px;
  height: 1px;
  background-color: #d5dade;
}
.terminal .content__info span {
  font-weight: 700;
}
.terminal .content__title {
  position: relative;
  z-index: 2;
  display: flex;
  padding: 32px 30px 27px;
}
.terminal .content__title:after {
  position: absolute;
  content: '';
  left: 0;
  bottom: 0;
